import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";

/**
 * Renders a responsive, interactive card for a single album (e.g., a photography or artwork collection).
 *
 * This component acts as a visual link to a detailed album page. It displays a cover image
 * with a title overlay and includes hover effects (scaling, shadow changes) to enhance user interaction.
 * It is designed to be reusable for different categories of albums by dynamically constructing the link
 * and translating content.
 *
 * @param {object} props - The component props.
 * @param {object} props.album - An object containing the album's data.
 * @param {string} props.album.id - The unique identifier for the album, used to construct the link URL.
 * @param {string} props.album.cover - The URL for the album's cover image.
 * @param {string} [props.album.coverAlt] - Optional, specific alt text for the cover image. If not provided, the translated title is used.
 * @param {string} props.album.titleKey - The translation key for the album's title, used with `react-i18next`.
 * @param {string} [props.album.fontClass] - Optional CSS class to apply a specific font to the title.
 * @param {'artwork' | 'photography'} props.albumType - The type of the album, used to construct the base path of the link URL.
 * @returns {JSX.Element} A link-wrapped card component.
 */
function AlbumCard({ album, albumType }) {
  const { t } = useTranslation();
  return (
    <Link
      to={`/${albumType}/${album.id}`}
      onClick={() => window.scrollTo(0, 0)}
      className="block group"
    >
      <div className="relative w-full h-64 rounded-lg overflow-hidden shadow-lg group-hover:shadow-xl transition-shadow duration-300">
        <img
  src={album.cover}
  alt={album.coverAlt || t(album.titleKey)}
  className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
  loading="lazy"
/>
        <div className="absolute inset-0 bg-black/30 group-hover:bg-black/40 transition-colors duration-300"></div>
        <div className="absolute inset-0 flex items-center justify-center p-4">
          <h3
            className={`text-4xl font-bold text-white text-center drop-shadow-lg transform group-hover:scale-110 transition-transform duration-300 ${album.fontClass}`}
          >
            {t(album.titleKey)}
          </h3>
        </div>
      </div>
    </Link>
  );
}

AlbumCard.propTypes = {
  album: PropTypes.object.isRequired,
  albumType: PropTypes.oneOf(["artwork", "photography"]).isRequired,
};

export default AlbumCard;