import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";
import AlbumCard from "./AlbumCard";

/**
 * Renders a section to showcase a collection of other albums for exploration.
 *
 * This component is designed to be used on an album detail page to suggest other
 * relevant albums to the user. It displays a title and a responsive grid of
 * `AlbumCard` components. If the list of `otherAlbums` is empty, the component
 * will render nothing.
 *
 * @param {object} props - The component props.
 * @param {'artwork' | 'photography'} props.albumType - The category of the albums being displayed (e.g., 'artwork', 'photography'). This is passed down to each `AlbumCard`.
 * @param {Array<object>} props.otherAlbums - An array of album objects to display. Each object should match the structure expected by the `AlbumCard` component.
 * @param {string} props.exploreTitleKey - The translation key for the section's title (e.g., "explore.other_artwork").
 * @returns {JSX.Element | null} A section with a grid of album cards, or null if there are no other albums to display.
 */
function ExploreOtherAlbums({ albumType, otherAlbums, exploreTitleKey }) {
  const { t } = useTranslation();

  if (otherAlbums.length === 0) {
    return null;
  }

  return (
    <div className="mt-16 pt-10 border-t border-white/20">
      <h2 className="text-3xl font-bold text-white text-center mb-8">
        {t(exploreTitleKey)}
      </h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 max-w-4xl mx-auto">
        {otherAlbums.map((album) => (
          <AlbumCard key={album.id} album={album} albumType={albumType} />
        ))}
      </div>
    </div>
  );
}

ExploreOtherAlbums.propTypes = {
  albumType: PropTypes.oneOf(["artwork", "photography"]).isRequired,
  otherAlbums: PropTypes.array.isRequired,
  exploreTitleKey: PropTypes.string.isRequired,
};

export default ExploreOtherAlbums;